<template>
	<section id="title_section" class="section">
		<div class="section_content">
			<div id="title_container">
				<h1 class="my_name no_text_select">Daniel-Junior Dubé</h1>
				<h2 class="under_my_name no_text_select">Portfolio</h2>
				<div class="title_social_media_links no_text_select">
					<hr>
					<a
						v-for="socialMediaLink in socialMediaLinks"
						:key="socialMediaLink.url"
						:href=socialMediaLink.url
					>
						<i
							:class=socialMediaLink.class
							:style="{color: socialMediaLink.color}"
							aria-hidden="true">
						</i>
					</a>
				</div>
				<!-- TODO: Add "employment status" under "social media" -->
				<!-- TODO: Add "learn more about me" at bottom, centered -->
			</div>
		</div>
		<a href="#" class="scrollto_next" v-scroll-to="{
				el: '#experiences_section',
				duration: scrollToSpeed,
				easing: 'ease-in-out'
		}">
			<i class="fa fa-3x fa-angle-double-down" aria-hidden="true"></i>
		</a>
	</section>
</template>

<script>
	import VueScrollTo from 'vue-scrollto';

	export default {
		name: "LandingPageTitleSection",
		props: ["scrollToSpeed"],
		data () {
			return {
				socialMediaLinks: [
					{ url: 'https://twitter.com/HEYIMFUZZ', class: "fa fa-twitter", color: "#55acee" },
					{ url: 'https://github.com/daniel-junior-dube', class: "fa fa-github", color: "#fff" },
					{ url: 'https://www.facebook.com/junior.dube.5', class: "fa fa-facebook-official", color: "#3B5998" },
					{ url: 'https://www.linkedin.com/in/daniel-junior-dubé-418a3381', class: "fa fa-linkedin", color: "#0077b5" },
				]
			}
		},
		directives: {
			scrollTo: VueScrollTo
		}
	}
</script>

<style lang="scss" scoped>
	@import "../../styles/global.scss";
	@import "../../styles/text_mixins.scss";
	@import "../../styles/background_mixins.scss";
	@import "../../styles/transition_mixins.scss";
	@import "../../styles/spacing_mixins.scss";

	.section {
		min-height: 100vh;
		align-items: center;
		flex-direction: column;
		@include background-image(
			url(/static/images/landing_bg.png),
			right bottom,
			no-repeat,
			auto 95%
		);
		& .section_content {
			& #title_container {
				display: inline-flex;
				flex-direction: column;
				@include spacing( 25px, 25px );
				.my_name {
					@include spacing( 0px 0px 10px 0px, 0px );
					@include text(
						$font-size: 3.8em,
						$shadow: 5px 5px 20px $less_transparent_black,
						$transform: uppercase,
						$font-weight: bold
					);
				}
				.under_my_name {
					font-weight: 400;
					@include spacing( 0px 0px 5px 0px, 0px );
					@include text(
						$font-size: 2.5em,
						$shadow: 5px 5px 20px $less_transparent_black,
						$transform: uppercase
					);
				}
				.title_social_media_links {
					& a {
						display: inline-block;
						margin-right: 5px;
						& i {
							font-size: 1.5em;
							@include background( $color: $less_transparent_black, $border-radius: 2px );
							@include spacing( 0px, 10px );
							@include transition( 0.2s ease );
							&:hover {
								@include background( $color: $light_contrast, $border-radius: 2px );
								@include transition( 0.1s ease );
							}
						}
						&:hover .fa-github {
							color: black !important;
						}
					}
				}
			}
		}
	}

	@media (min-width: 320px) and (max-width: 500px) {
		.section {
			& .section_content {
				& #title_container {
					.my_name {
						font-size: 3.2em;
					}
					.under_my_name {
						font-size: 2.2em;
					}
				}
			}
		}
	}

	@media (max-width: 321px) {
		.section {
			& .section_content {
				& #title_container {
					.my_name {
						font-size: 2.8em;
					}
					.under_my_name {
						font-size: 1.8em;
					}
				}
			}
		}
	}
</style>


